import React, {useState} from 'react';
import styles from "./Paginator.module.css";
import cn from "classnames";

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

    let pagesCount = Math.ceil(totalItemsCount / pageSize);// всего страниц

    let pages = []; // строка из пользователей
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i); // всего пользователей
    }

    let portionCount = Math.ceil(pagesCount / portionSize); // определяем порцию страниц 100/10 = 10 еще подумать
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1; // (1-1)* 10+1 = 1 порция /(2-1) * 10+1 = 11 //3 : 21
    let rightPortionPageNumber = portionNumber * portionSize;// 1*10 = 10 / 2+10 =20 / 3: 30


    return <div className={styles.paginator}>
        { portionNumber > 1 &&
        <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }

            {pages
                .filter(p => p >= leftPortionPageNumber && p<=rightPortionPageNumber)
                .map((p) => {
                return <span className={ cn({
                    [styles.selectedPage]: currentPage === p
                }, styles.pageNumber) }
                             key={p}
                             onClick={(e) => {
                                 onPageChanged(p);
                             }}>{p}</span>
            })}
        { portionCount > portionNumber &&
            <button onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button> }


    </div>
}

export default Paginator;